.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1200;
}

.modal {
  max-width: 90%;
  max-height: 90%;
}

.modal img {
  width: 100%;
  height: auto;
  display: block;
  border-radius: 4px;
}
